.app-wrapper{
  margin: 0 auto;
  display:grid;
  width: 1200px;
  height: 900px;
  grid-template-areas:"h h" "n c";
  grid-template-rows: 60px 2fr;
  grid-template-columns: 2fr 10fr;
  
}

.app-wrapper-content{
    grid-area: c;
    background-color: rgba(219, 219, 235, 0.658);
    width: 1100px;


  
}
